import * as AppUtils from "../../utils/app-utils";
import { ToggleUiState } from "../../stores/AppStateService";
export var AppDriverToggleStateDetector = function AppDriverToggleStateDetector() {};

AppDriverToggleStateDetector.getDriverInitialBrowserState = function () {
  var currentWidth = window.innerWidth;
  var driverWidth = parseInt(getComputedStyle(document.documentElement).getPropertyValue('--content-layout__left-layout_width'));
  var contentWidth = parseInt(getComputedStyle(document.documentElement).getPropertyValue('--content-layout__center-layout_width'));
  var minWidth = driverWidth + contentWidth;

  if (currentWidth < minWidth) {
    return ToggleUiState.Close;
  }

  return ToggleUiState.Open;
};

AppDriverToggleStateDetector.getDriverInitialState = function () {
  if (AppUtils.isBrowser()) {
    return AppDriverToggleStateDetector.getDriverInitialBrowserState();
  }

  return ToggleUiState.Close;
};

